.layoutGrid{margin:0 5%;display:grid;grid-template-columns:repeat(12, 1fr);grid-column-gap:10px;position:relative}.layoutGrid__content{padding-top:82px;grid-column:1 / span 12;display:grid;grid-template-columns:repeat(12, 1fr);grid-column-gap:10px;padding-bottom:58px}@media (min-width: 0px) and (max-width: 767px){.layoutGrid{margin:0 10px;grid-template-columns:repeat(4, 1fr);grid-column-gap:8px}.layoutGrid__content{padding-top:69px;grid-column:1 / span 4;grid-template-columns:repeat(4, 1fr);grid-column-gap:8px}}.header{width:90%;position:fixed;top:0;z-index:4;background-color:white;padding:20px 0;border-bottom:2px solid black;display:flex;justify-content:space-between;transition:all 0.3s;height:82px;box-sizing:border-box}.header__social{padding:0 5px;outline:none}.header.smaller{padding-bottom:10px;height:67px}.header a>img{transition:all 0.3s}.header.smaller a>img{height:30px;transition:all 0.3s}@media (min-width: 0px) and (max-width: 767px){.header{width:100%;padding:10px 0;height:69px}.header__social>img{width:30%}.header.smaller{height:57px}}.categories{grid-column:1 / span 12;display:grid;grid-template-columns:repeat(12, 1fr);grid-column-gap:16px;margin-top:40px}.categories__title{grid-column:1 / span 12}@media (min-width: 0px) and (max-width: 767px){.categories{grid-column:1 / span 4;grid-template-columns:repeat(4, 1fr);grid-column-gap:8px;margin-top:20px}.categories__title{grid-column:1 / span 4}}.categoryBox{grid-column:auto / span 3;height:256px;position:relative;overflow:hidden;border-radius:8px;box-shadow:0px 0px 0px rgba(0,0,0,0.05),0px 0px 0px rgba(0,0,0,0.13);transition:border .2s cubic-bezier(0.4, 0, 0.2, 1),box-shadow .2s cubic-bezier(0.4, 0, 0.2, 1);cursor:pointer;text-decoration:none}.categoryBox:hover{box-shadow:0px 7px 15px rgba(0,0,0,0.13),0px 0px 3px rgba(0,0,0,0.05)}.categoryBox__wrapper{width:100%;height:100%;position:relative;z-index:1}.categoryBox__title{width:100%;padding:8px 0;text-align:center;color:#fff;background-color:rgba(33,33,33,0.4)}.categoryBox__bg{transition:all 0.3s cubic-bezier(0.09, 0.5, 0.26, 0.9);position:absolute;width:100%;height:100%;top:0;left:0;background-repeat:no-repeat;background-size:cover;background-position:center center;transform:scale(1)}.categoryBox:hover .categoryBox__bg{transform:scale(1.1)}@media (min-width: 0px) and (max-width: 767px){.categoryBox{height:128px;grid-column:auto / span 2}.categoryBox:nth-child(4){margin-top:12px}.categoryBox:nth-child(5){margin-top:12px}}.onlineShopsSection{grid-column:1 / span 12;position:relative;border-radius:8px;margin-top:40px;display:flex;align-items:center}.onlineShopsSection__image{height:300px}.onlineShopsSection__itemWrapper{display:flex}.onlineShopsSection__item{border:1px solid rgba(147,146,152,0.5);border-radius:8px;overflow:hidden;padding:5px;margin-right:20px;box-shadow:0px 0px 0px rgba(0,0,0,0.05),0px 0px 0px rgba(0,0,0,0.13);transition:border .2s cubic-bezier(0.4, 0, 0.2, 1),box-shadow .2s cubic-bezier(0.4, 0, 0.2, 1);transition:all 0.3s cubic-bezier(0.09, 0.5, 0.26, 0.9);cursor:pointer}.onlineShopsSection__item:hover{box-shadow:0px 4px 10px rgba(0,0,0,0.13),0px 0px 3px rgba(0,0,0,0.05);transform:scale(1.1)}.onlineShopsSection__text{padding-left:50px}.onlineShopsSection__description{padding-bottom:30px}@media (min-width: 0px) and (max-width: 767px){.onlineShopsSection{grid-column:1 / span 4;display:block;margin-top:20px}.onlineShopsSection__image{width:100%;height:auto}.onlineShopsSection__itemWrapper{justify-content:center}.onlineShopsSection__text{padding-left:0}.onlineShopsSection__description{padding-bottom:20px}}.about{grid-column:1 / span 12;text-align:justify;margin-top:40px}.about__wrap{display:flex}.about__image{width:40%;margin:auto}.about__text{width:40%;margin-right:auto}.about__text>p{padding:5px 0}@media (min-width: 0px) and (max-width: 767px){.about{grid-column:1 / span 4;margin-top:20px}.about__wrap{display:block}.about__image{width:100%;padding-top:10px}.about__text{padding:0;width:auto}}.location{grid-column:1 / span 12;margin-top:40px}.location__container{display:flex;padding-bottom:20px}.location__itemWrap{display:flex;margin:20px 0}.location__text{display:flex;flex-direction:column;padding-left:10px}.location__title{padding-bottom:5px}.location__map{flex:1 1;margin-left:30px}@media (min-width: 0px) and (max-width: 767px){.location{grid-column:1 / span 4;margin-top:20px}.location__container{display:block}.location__map{margin-left:0}}.brands{grid-column:1 / span 12;display:grid;grid-template-columns:repeat(4, 1fr);grid-column-gap:16px}.brands__box{grid-column:auto / span 2}@media (min-width: 0px) and (max-width: 767px){.brands{grid-column:1 / span 4;grid-template-columns:repeat(4, 1fr);grid-column-gap:8px}.brands__box{grid-column:auto / span 1}}.galleryWrapper{grid-column:1 / span 12;display:flex;flex-wrap:wrap;padding:40px 0;justify-content:space-evenly}.galleryWrapper__image{border:5px solid #413f44;box-sizing:border-box;padding:20px;margin:10px;border-radius:8px;max-width:300px}@media (min-width: 0px) and (max-width: 767px){.galleryWrapper{grid-column:1 / span 4;padding:10px 0}.galleryWrapper__image{max-width:100%}}.categoryPage{grid-column:1 / span 12;margin-top:20px}.categoryPage__titleWrap{display:flex;align-items:center;padding:20px 0}.categoryPage__titleWrap>h1{padding-left:20px}.categoryPage__icon{cursor:pointer;display:flex}.categoryPage__text{padding-left:48px}@media (min-width: 0px) and (max-width: 767px){.categoryPage{grid-column:1 / span 4}.categoryPage__text{padding-left:16px}}.product{grid-column:auto / span 3;cursor:pointer;display:flex;flex-direction:column;border:1px solid grey;padding:10px;margin:5px;border-radius:8px;color:black;text-decoration:none;max-width:220px;justify-content:space-between}.product__title{padding:10px 0}.product__description{padding:10px 0}.product__price{font-weight:bold;line-height:120%;font-size:18px;padding:10px 0}@media (min-width: 0px) and (max-width: 767px){.product{grid-column:auto / span 4;max-width:100%}}.scrapedProducts{grid-column:1 / span 12;margin-top:40px;display:flex;flex-direction:column}.scrapedProducts__list{display:flex;flex-wrap:wrap}.scrapedProducts__button{align-self:baseline;background-color:rgba(147,146,152,0.5);margin:10px 0;color:black;text-decoration:none;border-radius:20px;padding:10px 15px;font-weight:bold;transition:border .2s cubic-bezier(0.4, 0, 0.2, 1),box-shadow .2s cubic-bezier(0.4, 0, 0.2, 1);transition:all 0.3s cubic-bezier(0.09, 0.5, 0.26, 0.9)}.scrapedProducts__button:hover{box-shadow:0px 4px 10px rgba(0,0,0,0.13),0px 0px 3px rgba(0,0,0,0.05)}@media (min-width: 0px) and (max-width: 767px){.scrapedProducts{grid-column:1 / span 4;margin-top:20px}}.h1{font-style:normal;font-weight:bold;line-height:90%;font-size:31.5px}.h2{font-style:normal;font-weight:bold;line-height:90%;font-size:21px}.h3{font-style:normal;font-weight:800;line-height:normal;font-size:14px;letter-spacing:0.1em;text-transform:uppercase}.h4{font-style:normal;font-weight:bold;line-height:90%;font-size:16px}.p{font-style:normal;font-weight:normal;font-size:14px}.sectionTitle{font-style:normal;font-weight:bold;line-height:90%;font-size:31.5px;padding:20px 0}@media only screen and (max-width: 600px){.h1{font-size:26px}.h2{font-size:24px}.h3{font-size:12px}.p{font-size:14px}.sectionTitle{font-size:26px;padding:10px 0}}h1,h2,h3,h4,p,body{margin:0}.footer{position:absolute;left:0;bottom:0;right:0;border-top:2px solid black}.footer__text{padding:20px 0}

